@import"https://cdn.jsdelivr.net/npm/galmuri@latest/dist/galmuri.css";.guide-text{display:block;position:absolute;top:50%;left:50%;width:90%;max-width:350px;padding:40px 30px;box-sizing:border-box;box-shadow:2px 2px 17px rgba(0,0,0,.2705882353);background-color:#0095ac;text-align:center;color:#fff;line-height:1.43;transform:translate(-54%, -45%);rotate:5deg;z-index:2}.guide-text::before{content:"";position:absolute;top:-20px;left:50%;width:150px;height:40px;border:3px dotted rgba(64,76,80,.5);border-top:0;border-bottom:0;box-shadow:0px -2px 7px 0px rgba(0,0,0,.25);background-color:rgba(255,255,255,.3);transform:translateX(-50%);mix-blend-mode:hard-light}.guide-text.small{max-width:240px;padding:40px 10px 20px;font-size:14px}.guide-text.small::before{width:90px}#guide{display:block;position:fixed;top:0;left:0;width:100%;height:100%;z-index:10}#guide .dim{position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(40,61,71,.8);z-index:1}#guide p{font-size:24px}#guide p b{font-family:"Galmuri14",Apple SD Gothic Neo,돋움,dotum,sans-serif;font-weight:bolder}#guide p em{font-family:"Galmuri14",Apple SD Gothic Neo,돋움,dotum,sans-serif;font-style:normal}#guide .guide-pc-hint{margin-top:15px;padding:10px;background-color:rgba(255,255,255,.15);border-radius:8px;font-size:16px;line-height:1.5}#guide button{display:block;width:80%;margin:30px auto 0;padding:12px 24px;box-sizing:border-box;border-radius:999px;background-color:#fff;font-size:30px;font-weight:700;color:#0095ac}#guide .check-group{display:inline-block;position:relative;margin-top:20px}#guide .check-group input{position:absolute !important;clip:rect(1px 1px 1px 1px);clip-path:polygon(0 0, 0 0, 0 0);padding:0 !important;border:0 !important;height:1px !important;width:1px !important;overflow:hidden}#guide .check-group label{padding-left:24px;font-size:15px;font-family:"Galmuri14",Apple SD Gothic Neo,돋움,dotum,sans-serif;vertical-align:middle}#guide .check-group label:before{content:"";position:absolute;top